.Compose {
	padding: 3rem 0;
}

.Compose h1 {
	padding: 0 0 2rem 0;
}

.Compose [type='text'] {
	width: 100%;
	padding: 1rem 0.5rem;
	font-family: 'consolas', 'Courier New', Courier, monospace;
	border: none;
	font-size: 0.9rem;
}

.Compose [type='text']:first-of-type {
	border-top-left-radius: 4px;
	border-top-right-radius: 4px;
	border: 1px solid #ffa7c4;
	border-bottom: 1px solid #ccc;
}

.Compose [type='text']:last-of-type {
	border-left: 1px solid #ffa7c4;
	border-right: 1px solid #ffa7c4;
}

.Compose textarea {
	resize: none;
	padding: 1rem;
	font-family: 'consolas', 'Courier New', Courier, monospace;
	width: 100%;
	font-size: 1rem;
	height: 40vh;
	border: 1px solid #ffa7c4;
	border-top: 1px solid #ccc;
	margin-top: -2px;
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
}

.buttons {
	margin-top: 0.5rem;
}

.buttons button {
	font-weight: bold;
	font-size: 1rem;
	padding: 0.35rem 1.5rem;
	border: none;
	border-radius: 4px;
	margin-right: 0.5rem;
	cursor: pointer;
	border: 1px solid #fff;
}

.buttons button:nth-of-type(1) {
	background: #fff;
	color: #282c35;
}

.buttons button:nth-of-type(2) {
	background: #282c35;
	color: #fff;
}
