.Blog {
	padding: 3rem 0;
}

.Blog h1 {
	padding: 0 0 2rem 0;
}

.info {
	border-radius: 10px;
	padding: 0.75rem 1rem;
	background: #3d4149;
	border: 1px solid #ffa7c4;
}

.content {
	padding: 2rem 0;
	line-height: 1.75;
	font-size: 1.18rem;
	font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
	opacity: 0.9;
}

.content p, .content li {
	margin-top: 1rem;
}

.content h1, .content h2, .content h3, .content h4, .content h5, .content h6 {
	margin-top: 2rem;
	margin-bottom: 1rem;
}

.Blog footer {
	font-weight: bold;
	color: #ffa7c4;
}
