*,
*::after,
*::before {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
		Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	outline: 0;
}

html,
body {
	font-size: 1rem;
	color: #fff;
	background-color: #282c35;
}

.App {
	width: 100vw;
	padding: 0 1.5rem;
	max-width: 750px;
	word-wrap: normal;
	margin: 0 auto;
}

.four-0-four {
	padding-top: 10rem;
	text-align: center;
	font-size: 2rem;
}


@media (max-width: 600px) {
	html,
	body {
		font-size: 0.8rem;
	}
}
