.Navbar {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	padding: 1.5rem 0;
}

.Logo img {
	border-radius: 50px;
	width: 5rem;
	height: 5rem;
	border: 2px solid #fff;
}

.Brand a {
	text-decoration: none;
}

.Brand h1 {
	color: #ffa7c4;
}

.Brand small {
	opacity: 0.5;
	float: right;
}
